.col-nav {
  width: 294px;
  padding-top: 40px;
  float: left; }
  .col-nav a {
    display: block;
    font-size: 20px;
    color: #6d6d70;
    font-weight: lighter;
    line-height: 20px;
    margin-bottom: 48px;
    text-align: center;
    float: left; }
    .col-nav a span {
      float: left;
      margin-left: 110px; }
    .col-nav a .arrow {
      display: none;
      margin-left: 8px; }
    .col-nav a:hover, .col-nav a.active {
      color: #d88259;
      font-weight: normal; }
      .col-nav a:hover .arrow, .col-nav a.active .arrow {
        display: inline-block;
        background: url(../images/icon/aboutus_arrow_right.png) no-repeat -5px 0;
        height: 20px;
        width: 10px; }
